import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { Layout } from "../../components/Layout";

const ViewClub = () => {
  const [clubs, setClubs] = useState([]);

  const getClubs = async () => {
    try {
      const res = await axios.get("/api/v1/admin/get-all-clubs", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        setClubs(res.data.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getClubs();
  }, []);

  return (
    <Layout>
      <div className="bg-gray-900 min-h-screen p-4">
        <h1 className="text-3xl font-bold text-white mb-6 text-center">
          Clubs at NIT Rourkela
        </h1>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {clubs.map((club) => (
            <div
              key={club._id}
              className="bg-gray-800 rounded-lg shadow-lg overflow-hidden transition-transform transform hover:scale-105 duration-300"
            >
              <div className="w-full h-80">
                <img
                  src={club.img[0]}
                  alt={club.name}
                  className="w-full h-full object-cover"
                />
              </div>
              <div className="p-4">
                <h2 className="text-xl font-bold text-white mb-2">
                  {club.name}
                </h2>
                <p className="text-gray-400 mb-1">Type: {club.clubType}</p>
                <p className="text-gray-400 mb-1">ID: {club.clubId}</p>
                <p className="text-gray-400 mb-2">
                  Established: {new Date(club.date).toLocaleDateString()}
                </p>
                <Link
                  to={`/admin/club/${club.clubId}`}
                  className="inline-block bg-green-500 text-white py-2 px-4 rounded hover:bg-green-600 transition-colors duration-200"
                >
                  View and Update
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
};

export default ViewClub;
